//IMPORT VARIABLES
@import "../variables/variables.scss";

.container_modal{
    padding: 0 10px;
    width: 100%;
    height: 100%;
    position: fixed;
    top:0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $bcgModal;
    z-index: -1;
    transform: scale(0);
    transition: all .2s ease-in-out;
}
//STYLE TO FIRST MODAL WINDOW
.window_car{
    position: relative;
    padding: 20px;
    flex:0 1 90%;
    display: flex;
    justify-content: space-between;
    border-radius: 15px;
    background-color: $white;
    transition: all .2s ease-in-out;
}
.window_car_f_part{
    text-align: start;
    max-width: 600px;
    margin-right: 20px;
}
.car_title{
    margin-bottom:10px;
}
.car_list{
    padding-left: 15px;
}
.car_el_list{
    margin-bottom: 6px;
    font-size: 18px;
    line-height: 24px;
}
.car_description{
    line-height: 20px;
    margin: 15px 0 10px ;
}
.window_car_s_part{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.window_car_photo{
    max-width: 500px;
    border-radius: 5px;
}
.box_btn{
    margin: 15px auto 0 auto;
    display: flex;
    justify-content: space-between;
    width: 300px;
}
.car_btn{
    padding: 10px 20px;
    color: white;
    border: 0;
    background-color:$ligthBlue ;
    border-radius: 4px;
    transform: scale(1);
    box-shadow: 3px 3px 3px grey;
    transition: all .3s ease-in-out;
    cursor: pointer;
    &:hover{
      box-shadow: 0px 0px 0px grey;
      transform: scale(0.9);
    }
}

//STYLE TO SECOND MODAL WINDOW 
.modal_reguest{
    flex: 0 1 700px;
    padding: 50px 20px;
    border-radius: 15px;
    background-color: $white;
    transition: all .2s ease-in-out;
}
.reguest_box_btn{
    margin: 30px auto 0 auto;
    display: flex;
    justify-content: space-between;
    max-width: 400px;
}
.reguest_btn{
    padding: 10px 20px;
    color: white;
    border: 0;
    border-radius: 4px;
    background-color:$darkBlue;
    transform: scale(1);
    box-shadow: 3px 3px 3px grey;
    transition: all .3s ease-in-out;
    cursor: pointer;
    &:hover{
      box-shadow: 0px 0px 0px grey;
      transform: scale(0.9);
    }
}
