//IMPORT VARIABLES
@import "components/variables/variables.scss";

//MIN STYLE
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
a {
  text-decoration: none;
}
ul,
li {
  list-style: none;
}
body {
  font-family: $raleway_500;
  font-size: 16px;
  overflow-x: hidden;
  background-color: $bgGrey;
}
a,
button,
label,
input {
  font-size: inherit;
  line-height: inherit;
}
a,
button,
label,
p,
span,
div,
li {
  color: inherit;
}
input[type="radio"]{
  display: none;
}
//STYLE TO MAKE BODY LOKED  
body.__lock {
  overflow-y: hidden;
}
//GENERAL STYLE TO TITLE
h2,
h3,
h4{
  font-family: $raleway_600;
}
h2 {
  font-size: 40px;
  line-height: 40px;
}
h3 {
  font-size: 28px;
  line-height: 34px;
}
h4{
  font-size: 24px;
  line-height: 26px;
}

.container {
  padding: 50px 10px 70px 10px;
  max-width: $widthFS;
  display: flex;
  justify-content: space-between;
}
.list-cars {
  margin-left: auto;
  max-width: 800px;
  text-align: center;
}
.box-car {
  padding: 20px;
  margin-bottom: 50px;
  display: flex;
  flex-direction: column;
  border-radius: 20px;
  box-shadow: 2px 2px 2px grey;
  background-color: $white;
  transition:  all .2s ease-in-out;
}
//STYLE TO HIDDEN CAR
.box-car.hidden-by-power,
.box-car.hidden{
  width: 0;
  height: 0;
  visibility: hidden;
  position: absolute;
  top:0;
  right: -100%;
  opacity: 0;
}
.name-car {
  margin-bottom: 20px;
}
.euro {
  margin: 0px 0 15px 0;
}
.power {
  margin: 10px 0 10px 0;
}
// STYLE TO IMG
.cars_photo {
  max-width: 100%;
  border-radius: 20px;
}
.car-btn{
  padding: 10px 40px;
  margin: 0 auto;
  max-width: 300px;
  background-color: $blue;
  border: none;
  border-radius: 4px;
  color: white;
  box-shadow: 3px 3px 3px grey;
  transition: all .3s ease-in-out;
  transform: scale(1);
  cursor: pointer;
  &:hover{
    box-shadow: 0px 0px 0px grey;
    transform: scale(0.9);
  }
}

// STYLE TO MODAL
#containerModal._open{
  z-index: 10;
  transform: scale(1);
  #modalCar{
      opacity: 1;
  }
}
#container_request._open{
  z-index: 10;
  transform: scale(1);
  #window_request{
      opacity: 1;
  }
}

//STYLE TO SHOW HIDDEN TEXT
.show_description{
  padding: 20px 30px;
  width: 50%;
  position: absolute;
  bottom: 10px;
  right: 10px;
  z-index: -1;
  opacity: 0;
  background-color: $white;
  transition: all .3s ease-in-out;
}
.show_description.show{
  z-index: 20;
  opacity: 1;
}
// STYLE TO INPUT
.filterLabel{
  padding-left: 15px;
  display: flex;
  transition: all .3s ease-in-out;
  margin-bottom: 15px;
  &::before{
    content: '';
    width: 20px;
    height: 20px;
    border-radius: 4px;
    border: 2px solid $colorText;
    margin-right: 5px;
    background:transparent;
    transition: all .3s ease-in-out;
  }
}
input[type="radio"]:checked + .filterLabel::before{
  border: 2px solid $blue;
  background: url(https://www.nicepng.com/png/full/137-1371483_blue-check-mark-icon-png-.png) no-repeat center center /cover;
}